<template>
  <div>
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-statistics' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <div class="query-time-container statistics">
          <el-select v-model="domain" placeholder="请选择" @change="onChangeDomain" size="small"
            style="margin-right: 10px;">
            <el-option v-for="item in domainList" :key="item.domain" :label="item.domain" :value="item.domain">
            </el-option>
          </el-select>
          <el-select v-model="valueTime" placeholder="Select" v-show="isShowSelectTime" @change="onChangeSelectTime"
            size="small">
            <el-option v-for="item in optionTime" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <div v-show="!isShowSelectTime">
            <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="-" start-placeholder="开始时间"
              end-placeholder="结束时间" @change="changeTimeline" size="small" />
          </div>
          <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" size="small" />
        </div>
        <div class="statistics-container">
          <el-row>
            <!-- <el-col :span="6"> -->
            <div class="left-content">
              <div class="content">
                <p><i class="el-icon-s-custom"></i>用户</p>
              </div>
              <div class="progress-text">
                <div class="text-box">
                  <div class="item">
                    <div class="title">请求次数</div>
                    <div class="detail">{{ tableData.total_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求IP数量</div>
                    <div class="detail">{{ tableData.unique_ips }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求设备数量</div>
                    <div class="detail">{{ tableData.jxwaf_devid_count }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求成功次数</div>
                    <div class="detail">{{ tableData.successful_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求4xx失败次数</div>
                    <div class="detail">{{ tableData.error_4xx_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求5xx失败次数</div>
                    <div class="detail">{{ tableData.error_5xx_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求平均耗时</div>
                    <div class="detail">{{ tableData.avg_request_time_ms }}</div>
                  </div>
                  <div class="item">
                    <div class="title">请求耗时中位数</div>
                    <div class="detail">{{ tableData.median_request_time_ms }}</div>
                  </div>
                </div>
              </div>
            </div>
            <!-- </el-col>
            <el-col :span="12"> -->
            <div class="middle-content">
              <div class="progress-left">
                <div class="progress-item">
                  <div class="line-box">
                    <div class="line line-1"></div>
                    <i class="el-icon-arrow-right arrow-1"></i>
                  </div>
                </div>

              </div>
              <div class="middle-box">
                <div class="content">
                  <p class="jxwaf-box"><i class="jxwaf-icon"><img src="../assets/images/logo.png" alt="logo"></i>WAF
                  </p>

                </div>
                <div class="progress-text">
                  <div class="text-box">

                    <div class="item">
                      <div class="title">攻击次数</div>
                      <div class="detail">{{ tableData.attack_attempts }}</div>
                    </div>
                    <div class="item">
                      <div class="title">拦截次数</div>
                      <div class="detail">{{ tableData.intercepted_attempts }}</div>
                    </div>
                    <div class="item">
                      <div class="title">攻击IP数量</div>
                      <div class="detail">{{ tableData.unique_ips_attack }}</div>
                    </div>
                    <div class="item">
                      <div class="title">拦截IP数量</div>
                      <div class="detail">{{ tableData.unique_ips_intercepted }}</div>
                    </div>
                    <div class="item">
                      <div class="title">攻击设备数量</div>
                      <div class="detail">{{ tableData.jxwaf_devid_attack }}</div>
                    </div>
                    <div class="item">
                      <div class="title">拦截设备数量</div>
                      <div class="detail">{{ tableData.jxwaf_devid_intercepted }}</div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="progress-right">
                <div class="progress-item">
                  <div class="line-box">
                    <div class="line line-2"></div>
                    <i class="el-icon-arrow-right arrow-2"></i>
                  </div>
                </div>

              </div>
            </div>
            <!-- </el-col>
            <el-col :span="6"> -->
            <div class="right-content">
              <div class="content">
                <p><i class="el-icon-s-platform"></i>业务服务器</p>

              </div>
              <div class="progress-text">
                <div class="text-box">
                  <div class="item">
                    <div class="title">回源次数</div>
                    <div class="detail">{{ tableData.total_upstream_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源IP数量</div>
                    <div class="detail">{{ tableData.upstream_unique_ips }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源设备数量</div>
                    <div class="detail">{{ tableData.upstream_jxwaf_devid_count }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源成功次数</div>
                    <div class="detail">{{ tableData.successful_upstream_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源4xx失败次数</div>
                    <div class="detail">{{ tableData.error_4xx_upstream_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源5xx失败次数</div>
                    <div class="detail">{{ tableData.error_5xx_upstream_requests }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源平均耗时</div>
                    <div class="detail">{{ tableData.avg_upstream_time_ms }}</div>
                  </div>
                  <div class="item">
                    <div class="title">回源耗时中位数</div>
                    <div class="detail">{{ tableData.median_upstream_time_ms }}</div>
                  </div>
                </div>
              </div>
            </div>
            <!-- </el-col> -->
          </el-row>
        </div>
        <div style="padding: 15px 0 0 0;border-top: 1px solid #ebeef5;color: #4e5969;">
          <p>业务服务器请求情况</p>
        </div>
        <div class="demo-block">
          <el-table :data="tableDataDetail" style="width: 100%">
            <el-table-column prop="Host" label="网站" width="180"></el-table-column>
            <el-table-column prop="UpstreamAddr" label="业务服务器"></el-table-column>
            <el-table-column label="回源请求状态">
              <template #default="scope">
                <p><span class="col-item-protection-title">请求次数：</span>
                  <el-tag size="small">{{ scope.row.TotalRequests }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求IP数量：</span>
                  <el-tag size="small">{{ scope.row.UniqueSrcIPs }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求设备数量：</span>
                  <el-tag size="small">{{ scope.row.UpstreamJxwafDevidCount }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求成功次数：</span>
                  <el-tag size="small">{{ scope.row.SuccessfulRequests }}</el-tag>
                </p>

                <p><span class="col-item-protection-title">请求4xx失败次数：</span>
                  <el-tag size="small">{{ scope.row.Error4xxRequests }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求5xx失败次数：</span>
                  <el-tag size="small">{{ scope.row.Error5xxRequests }}</el-tag>
                </p>
              </template>
            </el-table-column>
            <el-table-column label="回源请求耗时">
              <template #default="scope">
                <p><span class="col-item-protection-title">请求平均耗时：</span>
                  <el-tag size="small">{{ scope.row.AvgUpstreamTimeMs }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求耗时中位数：</span>
                  <el-tag size="small">{{ scope.row.MedianUpstreamTimeMs }}</el-tag>
                </p>
                <p><span class="col-item-protection-title">请求最长耗时：</span>
                  <el-tag size="small">{{ scope.row.MaxUpstreamTimeMs }}</el-tag>
                </p>

                <p><span class="col-item-protection-title">请求最短耗时：</span>
                  <el-tag size="small">{{ scope.row.MinUpstreamTimeMs }}</el-tag>
                </p>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  </div>

</template>
<script>
import { mixin, JXAjax, formatterDateTime } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      dataSearch: "",
      loadingPage: false,
      loading: false,
      tableData: {},
      tableDataDetail:[],
      isShowSelectTime: true,
      valueTime: "1w",
      domainList: [],
      domain: "",
      hasDomain: false,
      pickerTime: [],
      optionTime: [
        { value: '1h', label: '1小时' },
        { value: '1d', label: '24小时' },
        { value: '1w', label: '7天' },
        { value: '1m', label: '30天' },
        { value: 'default', label: '自定义' }
      ],
    };
  },
  computed: {
    rules() {
      return {
      }
    }
  },
  
  mounted() {
    this.getdomainListData();
    this.onChangeSelectTime()
    this.getDataConf();
  },
  methods: {
    getdomainListData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_domain_list",
        {},
        function (response) {
          t.loadingPage = false;
          t.domainList = response.data.message;
          t.domainList.unshift({ domain: "全部" });
          t.domain = t.domainList[0].domain;
        },
        function () {
          t.loadingPage = false;
        },

      );
    },
    getDataConf() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_sys_report_conf_conf",
        {},
        function (response) {
          t.loadingPage = false;
          t.logSource = response.data.message.report_conf;
          if (t.logSource == "false") {
            t.$message({
              duration: 0,
              showClose: true,
              message: "日志查询功能未配置，" + "<a href='/#/sys-report-conf' class='error-message-btn'>" + "点击前往配置" + "</a>",
              type: "warning",
              dangerouslyUseHTMLString: true,
            });
          } else {
            t.getDataConfLog();
          }
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    getDataConfLog() {
      var t = this;
      JXAjax(
        "post",
        "/waf/waf_get_sys_log_conf",
        {},
        function (response) {
          t.loadingPage = false;
          t.log_all = response.data.message.log_all;
          t.log_conf_remote = response.data.message.log_conf_remote;
          if (t.log_all == "true" && t.log_conf_remote =="true") {
            t.getData();
            t.getDataDetail();
          } else {
            t.$message({
              duration: 0,
              showClose: true,
              message: "日志传输功能未开启全流量日志记录，" + "<a href='/#/sys-log-conf' class='error-message-btn'>" + "点击前往配置" + "</a>",
              type: "warning",
              dangerouslyUseHTMLString: true,
            });
          }
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    getData() {
      var t = this;
      var _url = "/soc/soc_query_request_statistics"
      var _data = {
        from_time: formatterDateTime(t.pickerTime[0]),
        to_time: formatterDateTime(t.pickerTime[1]),
      }
      if (t.hasDomain) { 
        _url = "/soc/soc_query_domain_request_statistics"
        _data.domain = t.domain;
      }
      JXAjax(
        "post",
        _url,
        _data,
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    getDataDetail() {
      var t = this;
      var _url = "/soc/soc_query_request_statistics_detail";
      var _data = {
        from_time: formatterDateTime(t.pickerTime[0]),
        to_time: formatterDateTime(t.pickerTime[1]),
      };
      if (t.hasDomain) {
        _url = "/soc/soc_query_domain_request_statistics_detail"
        _data.domain = t.domain;
      }
      JXAjax(
        "post",
        _url,
        _data,
        function (response) {
          t.loadingPage = false;
          t.tableDataDetail = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onChangeSelectTime() {
      var t = this;
      if (t.valueTime == 'default') {
        t.isShowSelectTime = false;
        t.pickerTime = [
          new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
          new Date()
        ];
      } else {
        t.isShowSelectTime = true;
        if (t.valueTime == '1h') {
          t.pickerTime = [
            new Date(new Date().getTime() - 1 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1d') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1w') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 7),
            new Date()
          ];
        }
        if (t.valueTime == '1m') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30),
            new Date()
          ];
        }
      }
    },
    changeTimeline(event) {
      var t = this;
      if (event == null) {
        t.isShowSelectTime = true;
        t.valueTime = '1w'
      } else {
        t.isShowSelectTime = false;
      }
    },
    onChangeSearch() {
      this.onChangeSelectTime()
      this.getData()
      this.getDataDetail()
    },
    onChangeDomain(value) {
      if (value != "全部") {
        this.hasDomain = true;
      } else { 
        this.hasDomain = false;
      }
      this.onChangeSearch();
    },
  },
};
</script>
<style>
.statistics-container .middle-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - 630px);
}
.statistics-container .left-content,
.statistics-container .right-content,.statistics-container .middle-box {
  width: 230px;
}
.statistics-container .left-content,.statistics-container .right-content, .middle-box {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
  padding: 20px 20px 40px 20px;
  margin: 20px;
}

.statistics-container .item{
  display: flex;
    justify-content: center;
    margin:10px;
    color: rgb(144, 147, 153);
    font-size: 14px;
}
.statistics-container .title{
  display: flex;
  height: auto;
  line-height: 1.5;
  width: 80%;
  justify-content: left;
  color: rgb(144, 147, 153);
  font-size: 14px;
}
.statistics-container .detail {
  display: flex;
  height: auto;
    line-height: 1.5;
    width: 20%;
}

.statistics-container .middle-box {
  position: relative;
}
.statistics-container .progress-item {
  display: flex;
}
.statistics-container .el-icon-arrow-right {
  font-size: 52px;
  font-weight: bold;
  color: #409eff;
  display: block;
  position: relative;
  opacity: 0;
  animation: opacity 3s ease-in-out infinite;
}
.statistics-container .line-box {
  position: relative;
  line-height: 1;
  display: flex;
  align-items: center;
  padding: 27px 0px;
  width: 100%;
}
.statistics-container .line {
  display: block;
  width: 100%;
  height: 6px;
  background: #409eff;
  border-radius: 5px;
  animation: progress 3s ease-in-out infinite;
  opacity: 0;
}
.statistics-container .el-icon-arrow-right:before {
  position: absolute;
  top: -26px;
  left: -32px;
}

@keyframes progress {
  0% {
    opacity: 0;
    width: 0%;
  }
  100% {
    opacity: 1;
    max-width: 100%;
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

 .statistics-container .content i{
  margin-right: 12px;
  display: inline-block;
  color: #1d71ce;
}
.statistics-container .jxwaf-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.statistics-container .jxwaf-icon {
  width: 26px;
  line-height: 26px;
}
.statistics-container .jxwaf-icon img {
  width: 100%;
}
.statistics-container .content p {
  text-align: center;
    font-size: 26px;
    font-weight: 600;
    line-height: 1.5;
  color: #1d71ce;
}

.statistics-container .middle-content {
  position: relative;
}
.statistics-container .progress-left {
  position: absolute;
  top: 42%;
  left: 0;
  width: calc(50% - 160px);
}
.statistics-container .progress-right{
  position:absolute;
  top:42%;
  left: calc(50% + 160px);
  width: calc(50% - 160px);
}
.statistics-container .progress-right .arrow-2{
  animation-delay: 1.5s
}
.statistics-container .progress-right .line-2 {
  animation-delay: 1.5s
}
.statistics-container .progress-text {
  width: 100%;
  display: flex;
  justify-content: center;
}
.statistics-container .text-box {
  width: 100%;
  border-radius: 5px;
  background-color: #1d71ce12;
  padding: 10px;
  box-sizing: border-box;
  max-width: 200px;
  margin-top: 15px;
  border: 1px solid #ebeef5;
}
.query-time-container.statistics .el-button {
  margin-left: 10px;
}
.query-time-container.statistics {
  display: flex;
  padding-bottom: 15px;
  border-bottom: 1px solid rgb(235, 238, 245);
}
/* .statistics-container .left-content {
  background-image: url("../assets/images/kh.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.statistics-container .right-content {
  background-image: url("../assets/images/yw.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
} */
.col-item-protection-title {
  display: inline-block;
  width: 160px;
}
.el-table__body p {
  font-size: 14px;
  line-height: 30px;
}
</style>
